import React from 'react'
import styled from 'styled-components'

import color from '../color-css/color'

const Container = styled.div<PropsActive>`
  background: ${props => props.active ? "#E2F1F8" : "#F3F3F3"};
  border-radius: 0.625rem;
  opacity: 1;
  padding: ${props => props.padding ? props.padding : '19px 39.5px 18px 38.5px'};
  transition: background 0.5s ease-in-out;
  transition: border 0.5s ease-in-out;
  border: 1px solid ${color.white};
  margin-right: 0.625rem;
  margin-top: 0.625rem;
  @media screen and (max-width: 768px) {
      width: 100%;
      align-items: center;
      justify-content: center;
      margin-right: 0px;
      margin-left: 0px;
      padding: 1.25rem 0;
  };
  cursor: pointer;
`
const RadioButtonContainerWrapper = styled.div`
  &:hover ${Container} {
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: pointer;
  }
`
const StyledTitle = styled.div<PropsActive>`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: -0.15px;
  text-align: center;
  width:${props => props.widthSize ? props.widthSize : '100px'};
  color:${props => props.active ? "#052948" : "#757575"};
`
interface Props {
  active: boolean,
  onPress: any,
  title: string,
  padding?: string,
  widthSize?: string
}
interface PropsActive {
  active: boolean
  padding?: string
  widthSize?: string
}
export const ButtonItem: React.FC<Props> = (props) => {
  const { padding, widthSize } = props
  return (
    <RadioButtonContainerWrapper>
      <Container
        active={props.active}
        padding = {padding}
        onClick={() => { props.onPress() }}>
        <StyledTitle
          active={props.active}
          widthSize = {widthSize}
        >
          {props.title}
        </StyledTitle>
      </Container>
    </RadioButtonContainerWrapper>
  )
}
export default ButtonItem
