import React, { forwardRef } from 'react'
import styled from 'styled-components'
import ItemStep from './itemStep'

export type Props = {
  onItemStepClickedListenerProp?: (step: number) => void
  activateNextItemStepProp?: boolean
}

const FormStepContainer = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 0.3125rem;
  box-shadow: 0px 1.25rem 3.125rem #012F571A;
  padding: 2rem 3.125rem 1.9375rem;
  border: 1px solid #fff;
`
type Ref = HTMLDivElement

const FormStep = forwardRef<Ref, Props>((props, aRef) => {
  const { onItemStepClickedListenerProp, activateNextItemStepProp } = props
  const [idActive, changeIdActive] = React.useState(0)

  return (
    <FormStepContainer ref={aRef}>
      <ItemStep
        idActive={idActive}
        id={0}
        title={'Your work'}
        changeIdActive={() => {
          changeIdActive(0)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(0) : {}
        }} />

      <ItemStep
        idActive={idActive}
        id={1}
        title={'About you'}
        changeIdActive={() => {
          changeIdActive(1)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(1) : {}
        }} />
      <ItemStep
        widthSize = '14rem'
        idActive={idActive}
        id={2}
        title={'Membership agreement'}
        changeIdActive={() => {
          changeIdActive(2)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(2) : {}
        }} />
      <ItemStep
        widthSize = '11.1875rem'
        idActive={idActive}
        id={3}
        title={'Your contribution'}
        changeIdActive={() => {
          changeIdActive(3)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(3) : {}
        }} />
    </FormStepContainer>
  )
})

export default FormStep
