import React, { useState } from 'react'
import styled from 'styled-components'
import { isMobile } from 'react-device-detect'

import TextInput from '../TextInput/index'
import ValidationIndicator from '../ValidationIndicator'
import Tooltip from '../Tooltip'
import color from '../color-css/color'

const Label = styled.label`
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 1.125rem;
  text-align: left;
  color: ${color.blue_15_percent};
`

interface LabelIcon {
  onInputSucessAsLabelIconColor: boolean,
}

const LabelIcon = styled.span<LabelIcon>`
  color: ${props => props.onInputSucessAsLabelIconColor === true ? color.blue_darker : color.red};
`

export const DateInput  = (props: any) => {
  const [isSuccess, setIsSuccess] = useState(false);
  const [isVisible, setIsVisible] = useState(false);

  const handleOnInputSucess = (isSuccess: boolean) => {
    setIsSuccess(isSuccess)
  }

  const handleOnVisibility = (isVisible: boolean) => {
    setIsVisible(isVisible)
  }

  let message = "";
  if ( isSuccess === true) {
    message = "Thanks";
  } else {
    message = "Enter in the format DD/MM/YYYY";
  }
  
  if( isMobile ) {
    return (
      <div>
        <Label>Date of birth <LabelIcon onInputSucessAsLabelIconColor={isSuccess}>*</LabelIcon></Label>
        <TextInput placeholder="DD/MM/YYYY" name="dateOnMobile" maxLength={14} type="date"
        onInputSucess={(success:boolean) => {handleOnInputSucess(success)}}
        onVisibility={visible => handleOnVisibility(visible)}/>
        <ValidationIndicator visibility={isVisible} isCorrect={isSuccess} message={message}/>
        <Tooltip visibility={isVisible} tooltipName="date"
        tooltipTitle="Why do we need this?"
        tooltip_message="We need to know how old you are so 
        we can ensure you are being paid correctly for your age.
        This is especially important for junior workers." />
      </div>
    )
  }

  return (
    <div>
      <Label>Date of birth <LabelIcon onInputSucessAsLabelIconColor={isSuccess}>*</LabelIcon></Label>
      <TextInput placeholder="DD/MM/YYYY" name="dateOnDesktop" maxLength={14} type="text"
       onInputSucess={success => handleOnInputSucess(success)}
       onVisibility={visible => handleOnVisibility(visible)}/>
      <ValidationIndicator visibility={isVisible} isCorrect={isSuccess} message={message}/>
      <Tooltip visibility={isVisible} tooltipName="date"
      tooltipTitle="Why do we need this?"
      tooltip_message="We need to know how old you are so 
      we can ensure you are being paid correctly for your age.
      This is especially important for junior workers." />
    </div>
  )
}

export default DateInput;