import React from 'react'
import styled from 'styled-components'
import ButtonItem from './ItemButton'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons'

const StyledIconChecked = styled(FontAwesomeIcon)`
  color: #189126;
  width: 1.25rem !important;
  height: 1.25rem;
  text-align: center;
`
const Container = styled.div`
  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 1.25rem;
`
const ContainerInfo = styled.div`
  display: flex,
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  margin-top: 0.6875rem;
`

interface RadioButtonContaiter {
  className?: string
  visibility?: boolean
}

const RadioButtonContaiter = styled.div<RadioButtonContaiter>`
  display: ${props => props.visibility === true ? 'block' : 'none'};
  @media (max-width: 768px) {
    margin-top: 1.875rem;
  }
`

export type Props = {
  className?: string,
  onSelectTabProp?: (tabNumber: number) => void,
  label: string,
  title1: string,
  title2: string,
  title3: string,
  visibility: boolean
}

export const RadioButtonGroup: React.FC<Props> = ({
  className, onSelectTabProp, label, title1, title2, title3, visibility
}) => {
  const [activeId, changeActiveId] = React.useState(3)

  return (
    <RadioButtonContaiter
      className={className}
      visibility={visibility}
    >
      <Label>{label}
        {' '}
        <LabelIcon onInputSucessAsLabelIconColor={true}>*</LabelIcon>
      </Label>
      <Container>
        <ButtonItem
          active={activeId == 0}
          title={title1}
          onPress={() => {
            changeActiveId(0)
            onSelectTabProp ? onSelectTabProp(0) : {}
          }} />
        <ButtonItem
          active={activeId == 1}
          title={title2}
          padding = '19px 48px 18px 48px'
          widthSize = '82px'
          onPress={() => {
            changeActiveId(1)
            onSelectTabProp ? onSelectTabProp(1) : {}
          }} />
        <ButtonItem
          active={activeId == 2}
          title={title3}
          padding = '19px 24.5px 18px 23.5px'
          widthSize = '130px'
          onPress={() => {
            changeActiveId(2)
            onSelectTabProp ? onSelectTabProp(2) : {}
          }} />
      </Container>
      <ContainerInfo>
        <StyledIconChecked icon={faCheckCircle} style={{ display: activeId == 3 ? 'none' : 'block' }} />
      </ContainerInfo>
    </RadioButtonContaiter>
  )
}
export default RadioButtonGroup
