import React from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faCheck } from '@fortawesome/free-solid-svg-icons'

import ExpandableParagraph from '../ExpandableParagraph/'
import Tooltip from '../Tooltip'
import ValidationIndicator from '../ValidationIndicator'
import color from '../color-css/color'

export type Props = {
  description: string
  longtext: boolean
  widthSize?: string
  padding?: string
  title?: string
  tooltip?: boolean
  validateMessage?: string
  checkboxContent?: string
}
interface ContainerCheckboxProps {
  check: boolean
  widthSize?: string
  padding?: string
}
const ContainerCheckbox = styled.a<ContainerCheckboxProps>`
  width: ${props => props.widthSize ? props.widthSize : '133px'};
  background:${props => props.check ? '#E2F1F8' : '#EFF0F0'} ;
  transition: background 0.5s ease-in-out;
  border-radius: 0.625rem;
  opacity: 1;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  margin-top: 2.5rem;
  margin-bottom: 0.625rem;
  padding: ${props => props.padding ? props.padding : '18px 149px 17px 20px'};
  border: 1px solid ${color.white};
  cursor: pointer;
  @media screen and (max-width: 768px) {
    width: 100%;
    padding: 0.9375rem 0 13px 0;
  }
`
const ContainerCheckboxHover = styled.div`
  &:hover ${ContainerCheckbox} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
  }
`
const StyledIcon = styled(FontAwesomeIcon)<ContainerCheckboxProps>`
  color: ${props=>props.check ? '#ff6820' : '#757575'};
  width: 1.25rem !important;
  height: 1.25rem;
  text-align: center;
  @media screen and (max-width: 768px) {
    margin-left: 0.9375rem;
    width: 1rem !important;
  }
`

const StyledTitle = styled.span<ContainerCheckboxProps>`
  text-align: left;
  font: 500 1rem museo-sans, sans-serif;
  letter-spacing: -0.05px;
  color: ${props=>props.check ? color.blue_17_percent : '#757575'};
  opacity: 1;
  margin-left: 1.25rem;
`
const Container = styled.div`
  flex: 1;
  max-width: 38.75rem;
`
const StyledTitleMain = styled.div`
  text-align: left;
  font: 600 1rem;
  font-family: museo-sans, sans-serif;
  letter-spacing: -0.05px;
  color: #00AFD9
  opacity: 1;
  margin-bottom: 1.0625rem
`

export const CheckboxField: React.FC<Props> = (props: any) => {
  const { title, tooltip, widthSize, padding, validateMessage, checkboxContent } = props
  const [check, changeCheck] = React.useState<boolean>(false)

  return (
    <Container>
      <StyledTitleMain>
        {title}
        {' '}
        <span style={{ color: '#00AFD9', fontSize: '20px' }}>*</span>
      </StyledTitleMain>
      <ExpandableParagraph longtext={props.longtext}>
        {props.description}
      </ExpandableParagraph>
      <ContainerCheckboxHover>
        <ContainerCheckbox
          widthSize={widthSize}
          padding={padding}
          check={check}
          onClick={()=>{
            changeCheck(!check)
            }}>
          <StyledIcon
            check={check}
            icon={check?faCheck:faSquare}/>
          <StyledTitle check={check}>
            {checkboxContent}
          </StyledTitle>
        </ContainerCheckbox>
      </ContainerCheckboxHover>
      {tooltip === true && <Tooltip
        visibility = {check}
        widthSize="418px" 
        tooltipName="preExistingIssues"
        tooltipTitle="What does this mean?" 
        tooltip_message="For example, if you acquired an injury at work 
        <span style='font-weight: bold; font-style: italic'>before</span> signing up with the SDA, we aren’t able to provide specific 
        assistance with that issue beyond general advice. However, if you acquired 
        an injury at work after signing up with the SDA, we’re here to help in any way that we can. "
      />}
      {validateMessage && <ValidationIndicator isCorrect={check} message={validateMessage} visibility={check} />}
    </Container>
  )
}
export default CheckboxField
