import React from 'react'
import styled from 'styled-components'
import animationData from './check-blue.json'
import Lottie from 'react-lottie'

export type Props = {
  idActive: number
  id: number
  changeIdActive: object
  title: string
  widthSize?: string
}

interface StyledCounter {
  widthSize?: string
}

const StyledCounter = styled.a<StyledCounter>`
  width: ${props => props.widthSize ? props.widthSize : '7.5rem'};
  margin: auto;
  display: flex;
  cursor: pointer;
`
const StyledNum = styled.div`
  width: 1.75rem;
  margin-right: 0.5rem;
`
// @ts-ignore
const StyledNumSpan = styled.span`\n  width: 1.625rem;\n  height: 1.625rem;\n  line-height: 1.625rem;\n  font-size: 1rem;\n  text-align: center;\n  color: ${props => props.id <= props.idActive ? '#00AFD9' : props.hover ? '#00AFD9' : '#757575'};\n  background: ${props => props.id <= props.idActive ? '#E2F1F8' : props.hover ? '#E2F1F8' : '#F3F3F3'};;\n  border-radius: 0.875rem;\n  border: 1px solid #e2f1f8;\n  display: block;\n  font-weight: 600;\n  font: regular 1rem intro;\n font-family: intro, sans-serif`
const StyledDesc = styled.div`
    font-size: 1rem;
    line-height: 1.75rem;
    color: #012e56;
    flex:1,
`
// @ts-ignore
const StyledTitle = styled.span`\n text-align: left; \n font: 1rem museo-sans ,sans-serif; \n font-weight: ${props => props.id < props.idActive ? 900 : 500} ;\n letter-spacing: -1.05px;\n    color: ${props => props.id <= props.idActive ? '#012F57' : '#757575'};\n    opacity: ${props => props.id <= props.idActive ? 1 : 0.5};\n display: ${props => props.isMobile ? 'none' : 'inline'};\n`
const defaultOptions = {
  loop: false,
  autoplay: true,
  animationData: animationData,
}
export const ItemStep: React.FC<Props> = (props) => {
  const { idActive, id, changeIdActive, title, widthSize } = props
  const [hover, changeHover] = React.useState(false)
  const [isMobile, changeIsMobile] = React.useState(false)
  const [isStopped, changeIsStopped] = React.useState(false)

  React.useEffect(() => {
    changeIsMobile(typeof window !== 'undefined' && window.innerWidth < 768 ? true : false)
    // @ts-ignore
    window.addEventListener('resize', changeIsMobile(typeof window !== 'undefined' && window.innerWidth < 768 ? true : false))
    // @ts-ignore
    return window.removeEventListener('resize', changeIsMobile(typeof window !== 'undefined' && window.innerWidth < 768 ? true : false))
  }, [])
  return (
    <StyledCounter
      // @ts-ignore
      onClick={() => changeIdActive()}
      onMouseEnter={() => changeHover(true)}
      onMouseLeave={() => changeHover(false)}
      widthSize = {widthSize}
    >
      <StyledNum>
        <div style={{ height: 28, width: 28, position: 'relative' }}>
          {
            idActive > id ? (
              <Lottie
                options={defaultOptions}
                height={28}
                width={28}
                isStopped={isStopped}
              />
            )
              : (
                <StyledNumSpan
                  idActive={idActive}
                  // @ts-ignore
                  id={id}
                  hover={hover}
                >
                  {id + 1}
                </StyledNumSpan>
              )}
          <div style={{ position: 'absolute', top: 0 }}>
            {
              hover ? (
                <StyledNumSpan
                  idActive={idActive}
                  // @ts-ignore
                  id={id}
                >
                  {id + 1}
                </StyledNumSpan>
              )
                :
                null
            }
          </div>
        </div>
      </StyledNum>
      <StyledDesc>
        <StyledTitle
          idActive={idActive}
          // @ts-ignore
          id={id}
          hover={hover}
          isMobile={isMobile}
        >
          {title}
        </StyledTitle>
      </StyledDesc>
    </StyledCounter>
  )
}

export default ItemStep
