import React, { useState, useRef } from 'react'
import styled from 'styled-components'

import color from '../color-css/color'
import { FormData } from './types/FormData'
import { messagesIndexedByNameAttributeWithBooleanValue } from '../dataProcessing'
import TextInput from '../TextInput'
import PlacesAutocomplete from '../AddressAutosuggest-remake/index'
import DateInput from '../DateInput'
import SelectDropdown from '../SelectDropdown'
import ProgressButton, { ButtonStates } from '../ProgressButton'
import gender from '../../assets/data/gender.json'
import employmentStatus from '../../assets/data/employmentStatus.json'
import H3 from '../H3'

export type Props = {
    onFormValidatedProp: (isValidated: boolean) => void,
    onClickOnContinueButtonProp: (formData: any) => void
}

const FormStep2Container = styled.div`
    max-width: 655px;
    position: relative;
    animation-name: flickity;
    animation-duration: 1.5s;  
    animation-fill-mode: forwards;
    @keyframes flickity {
        0% {
            opacity:0;
        }
        100% {
            opacity:1;
        }
    };
    @media (max-width: 768px) {
        display: block;
        margin-left: 0;
        max-width: 100%;
    };
`

const FormStep2Form = styled.div`
    max-width: 654px;
    height: 100%;
    @media (max-width: 768px) {
        display: block;
        max-width: 100%;
    }
    margin-bottom:2.5rem;
`

const FormStep2FormContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 768px) {
        display: block;
    };
`

const InputFieldContainer = styled.div`
    margin-top: 2.1875rem;
`

const Wrapped = styled.div`
    width: 654px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 1.6875rem;
    margin-bottom: 1.875rem;
    @media (max-width: 768px) {
        display: block;
        width: 100%;
    }
`

const ProgressButtonPositioningDiv = styled.div`
    margin-top: 2.5rem;
    margin-bottom: 7.625rem;
`

const GenderWrapperMobile = styled.div`
    @media (max-width: 768px) {
        margin-top: 1.875rem;
    }
`

const FormStep2AboutYou: React.FC<Props> = ({
    onFormValidatedProp,
    onClickOnContinueButtonProp
}) => {

    interface validationByName {
        [label: string]: boolean,
    }

    interface ReactRef {
        [label: string]: React.RefObject<HTMLInputElement>,
    }

    let formData: FormData = {}

    const [validationInfo, updateValidationInfo] = useState(false)
    const Refs: ReactRef = {
        ["address"]: useRef<HTMLInputElement>(null),
        ["initNumber"]: useRef<HTMLInputElement>(null),
        ["city"]: useRef<HTMLInputElement>(null),
        ["stateOrTerritory"]: useRef<HTMLInputElement>(null),
        ["postcode"]: useRef<HTMLInputElement>(null),
        ["gender"]: useRef<HTMLInputElement>(null),
        ["employmentSatatus"]: useRef<HTMLInputElement>(null),
    }

    const validation: validationByName = {
        ["address"]: false,
        ["initNumber"]: false,
        ["city"]: false,
        ["stateOrTerritory"]: false,
        ["postcode"]: false,
        ["gender"]: false,
        ["employmentSatatus"]: false,
    }

    const getValidate = () =>
        validation["address"]
        && validation["initNumber"]
        && validation["city"]
        && validation["stateOrTerritory"]
        && validation["postcode"]
        && validation["gender"]
        && validation["employmentSatatus"]

    const inputFieldFactory = (children: React.ReactNode) => {
        return (
            <InputFieldContainer>
                {children}
            </InputFieldContainer>)
    }

    const createInputField = (
        label: string,
        placeHolderParam: string,
        nameParam: string,
        maxLength?: number,
        labelNotRequire?: string,
        aRef?: React.RefObject<HTMLInputElement>): React.ReactNode => {
        return (
            <TextInput
                placeholder={placeHolderParam}
                name={nameParam}
                type="text"
                maxLength={maxLength}
                {...{ label, message: messagesIndexedByNameAttributeWithBooleanValue, labelNotRequire }}
                onInputSucess={wasAnInputSuccess => {
                    validation[nameParam] = wasAnInputSuccess
                    updateValidationInfo(getValidate())
                }}
                ref={aRef}
            />
        )
    }

    const onFormIsValidated = () => {
        onFormValidatedProp(validationInfo)
        return validationInfo ? ButtonStates.enabled : ButtonStates.disabled
    }

    return (
        <FormStep2Container>
            <H3>
                About you
            </H3>
            <FormStep2Form>
                <FormStep2FormContainer>
                    <PlacesAutocomplete
                        ref={Refs["address"]}
                        onSelectedItemProp={wasSelected => {
                            validation["address"] = wasSelected
                            updateValidationInfo(getValidate())
                        }}
                    />

                    {inputFieldFactory(
                        createInputField("", "Type your unit number", "initNumber",
                            undefined, "Unit number (if applicable)", Refs["initNumber"])
                    )}

                    {inputFieldFactory(
                        createInputField("City", "Type your city", "city",
                            undefined, undefined, Refs["city"])
                    )}

                    {inputFieldFactory(
                        createInputField("State / territory", "Type your state / territory", "stateOrTerritory",
                            undefined, undefined, Refs["stateOrTerritory"])
                    )}

                    {inputFieldFactory(
                        createInputField("Postcode", "Type your postcode", "postcode",
                            undefined, undefined, Refs["postcode"])
                    )}

                    <Wrapped>
                        <DateInput />
                        <GenderWrapperMobile>
                            <SelectDropdown
                                label="Gender"
                                name="gender"
                                contentDefault="Select gender"
                                data={gender}
                                onSelectedItemProp={wasSelected => {
                                    validation["gender"] = wasSelected
                                    updateValidationInfo(getValidate())
                                }}
                                ref={Refs["gender"]}
                            />
                        </GenderWrapperMobile>
                    </Wrapped>

                    <SelectDropdown
                        label="Employment status"
                        name="employmentSatatus"
                        contentDefault="Select employment status"
                        data={employmentStatus}
                        onSelectedItemProp={wasSelected => {
                            validation["employmentSatatus"] = wasSelected
                            updateValidationInfo(getValidate())
                        }}
                        ref={Refs["employmentSatatus"]}
                    />

                </FormStep2FormContainer>

                <ProgressButtonPositioningDiv>
                    <ProgressButton
                        title="Continue"
                        buttonStates={
                            onFormIsValidated()
                        }
                        onClickCallbackProp={() => {
                            for (const key in Refs) {
                                if (Refs[key].current !== null) {
                                    formData = {
                                        ...formData,
                                        [key]: Refs[key].current?.value
                                    }
                                }
                            }
                            onClickOnContinueButtonProp(formData)
                        }}
                    />
                </ProgressButtonPositioningDiv>
            </FormStep2Form>

        </FormStep2Container >
    )
}

export default FormStep2AboutYou