import React, { useState, useEffect, forwardRef } from 'react'
import { useStaticQuery, graphql } from 'gatsby'

import styled from 'styled-components'
import useOnclickOutside from "react-cool-onclickoutside";

import { renderDateMobile } from './DateRenderer'
import { renderCreditCard, renderExpiryDate, renderBSB, renderAccountNumber } from './CardRenderer'
import { getMessageByName } from '../dataProcessing'
import { inputNumberOnly } from './InputNumberOnly'
import ValidationIndicator from '../ValidationIndicator'
import Tooltip from '../Tooltip'
import {
  validatePhoneNumber, validateEmail, validateDate,
  validatePostcode, handlePhoneNumber, handleDateOnDesktop,
  validateExpiryDate, validateCreditCard, validateBSB, validateAccountNumber
} from './utils'
import color from '../color-css/color'
import tooltipData from './tooltip.json'
import img from '../../assets/images/hand-point-up-1.png'

export type Props = {
  placeholder: string,
  widthSize?: string,
  name: string,
  maxLength?: number,
  type?: string,
  onInputSucess?: (isSuccess: boolean) => void,
  onVisibility?: (isVisible: boolean) => void,
  onShow?: (isShow: boolean) => void,
  inputFromProps?: string,
  value?: string
  onTypeText?: (textInputValue: string) => void
  readOnly?: boolean
  paddingSize?: string
}

interface InputField {
  widthSize?: string,
  placeholder: string,
  name: string,
  onInputSucess?: (isSucess: boolean) => boolean,
  onVisibility?: (isVisible: boolean) => boolean,
  onShow?: (isShow: boolean) => boolean,
  maxLength?: number,
  type?: string,
  onInputSucessAsBackgroundColor: boolean,
  inputFromProps?: string,
  value?: string,
  onTypeText?: (textInputValue: string) => void
  readOnly?: boolean
  paddingSize?: string,
}

const InputField = styled.input<InputField>`
  font-family: museo-sans, sans-serif;
  @media screen and (max-width: 768px) {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  width: ${props => props.widthSize ? props.widthSize : "15.6875rem"};
  height: 1.1875rem;
  background-color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue : color.grey};
  transition: background 0.5s ease-in-out;
  border: 1px solid ${color.white};
  outline: none;
  color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue_17_percent : color.grey_dark};
  font-size: 1rem;
  padding: ${props => props.paddingSize ? props.paddingSize : "0.8125rem 1.625rem 0.75rem 1.5625rem"};
  border-radius: 0.625rem;  
  ::placeholder,
  ::-webkit-input-placeholder {
    color: ${color.grey_darker};
    font-family: museo-sans, sans-serif;
    font-size: 1rem;
  }
  &:focus {
    ::-webkit-input-placeholder { color:transparent; }
    ::-moz-placeholder { color:transparent; }
    border: 1px solid ${color.blue_darker};
  }
`
interface IHoverWrapper {
  hoverIcon: string
}
const HoveringWrapper = styled.div<IHoverWrapper>`
  margin-bottom: 0.625rem;
  margin-top: 1.25rem;
  &:hover ${InputField} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: url(${props => props.hoverIcon}), auto;
  }
`

const Label = styled.label`
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 1.125rem;
  text-align: left;
  color: ${color.blue_15_percent};
`

interface LabelIcon {
  onInputSucessAsLabelIconColor: boolean,
}

const LabelIcon = styled.span<LabelIcon>`
  color: ${props => props.onInputSucessAsLabelIconColor === true ? color.blue_darker : color.red};
`

export type Ref = HTMLInputElement

const TextInput = forwardRef<Ref, Props>((props: any, aRef: any) => {
  const [inputSuccess, setInputSuccess] = useState(false);
  const [visibility, setVisibility] = useState(false);
  const [textOutput, setTextOutput] = useState('');
  const [isShow, setIsShow] = useState(false);
  
  useEffect(() => {
    setTextOutput(props.inputFromProps);
    setIsShow(false)

  }, [props.inputFromProps]);

  const handleValidation = (event: any) => {
    let inputGetter = event.target.value

    if (props.name === 'phoneNumber') {
      handlePhoneNumber(inputGetter, event)
    }
    else if (props.name === 'dateOnDesktop') {
      handleDateOnDesktop(inputGetter, event)
    }
    else if (props.name === 'dateOnMobile') {
      renderDateMobile(inputGetter)
    }
    else if (props.name === 'workSuburb') {
      inputGetter.length >= 3 ? setVisibility(true) : setVisibility(false);
      setTextOutput(inputGetter);
    }
    else if (props.name === 'address') {
      inputGetter.length >= 2 ? setIsShow(true) : setIsShow(false);
      setTextOutput(inputGetter);
    }
    else if (props.name === 'creditCard') {
      let result = renderCreditCard(inputGetter)
      if (inputGetter.length === 5 && inputGetter[4] !== ' ') {
        event.target.value = result
      }
      if (inputGetter.length === 10 && inputGetter[9] !== ' ') {
        event.target.value = result
      }
      if (inputGetter.length === 15 && inputGetter[14] !== ' ') {
        event.target.value = result
      }
      if (inputGetter.length > 15) {
        event.target.value = result
      }
    }
    else if (props.name === 'expiryDate') {
      let result = renderExpiryDate(inputGetter)
      if (inputGetter.length === 3 && inputGetter[2] !== ' ') {
        event.target.value = result
      }
      if (inputGetter.length > 3) {
        event.target.value = result
      }
    }
    else if (props.name === 'bsb') {
      let result = renderBSB(inputGetter)
      if (inputGetter.length === 4 && inputGetter[3] !== ' ') {
        event.target.value = result
      }
      if (inputGetter.length > 4) {
        event.target.value = result
      }
    }
    else if (props.name === 'accountNumber') {
      let result = renderAccountNumber(inputGetter)
      if (inputGetter.length === 5 && inputGetter[4] !== ' ') {
        event.target.value = result
      }
      if (inputGetter.length > 5) {
        event.target.value = result
      }
    }
  }

  const onSubmit = (event: any) => {
    let inputGetter = event.target.value;
    if (props.name === 'dateOnDesktop') {
      const unformatDate = inputGetter.split(' ').join('');
      setInputSuccess(() => validateDate(unformatDate))
    }
    else if (props.name === 'dateOnMobile') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'email') {
      setInputSuccess(() => validateEmail(inputGetter))
    }
    else if (props.name === 'firstName') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'lastName') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'phoneNumber') {
      const unformatPhoneNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validatePhoneNumber(unformatPhoneNumber))
    }
    else if (props.name === 'workSuburb') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'postcode') {
      setInputSuccess(() => validatePostcode(inputGetter))
    }
    else if (props.name === 'address') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'employer') {
      setInputSuccess(() => !!inputGetter);
    }
    else if (props.name === 'gender') {
      setTextOutput(props.inputFromProps)
      setInputSuccess(() => !!textOutput);
    }
    else if (props.name === 'creditCard') {
      const unformatCreditNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validateCreditCard(unformatCreditNumber))
    }
    else if (props.name === 'expiryDate') {
      setInputSuccess(() => validateExpiryDate(inputGetter))
    }
    else if (props.name === 'cvc') {
      setInputSuccess(() => inputGetter.length === 3)
    }
    else if (props.name === 'accountName') {
      setInputSuccess(() => !!inputGetter)
    }
    else if (props.name === 'bsb') {
      const unformatBSB = inputGetter.split(' ').join('');
      setInputSuccess(() => validateBSB(unformatBSB))
    }
    else if (props.name === 'accountNumber') {
      const unformatAccountNumber = inputGetter.split(' ').join('');
      setInputSuccess(() => validateAccountNumber(unformatAccountNumber))
    }
    else if (props.name === 'employmentSatatus') {
      setInputSuccess(() => !!inputGetter)
    }
    else if (props.name === 'initNumber') {
      setInputSuccess(true)
    }
    else if (
      props.name === 'city'
      || props.name === 'stateOrTerritory'
      || props.name === 'dayPaymentDeducted'
      || props.name === 'frequencyPaymentDeducted'
    ) {
      setInputSuccess(() => !!inputGetter)
    }

    setVisibility(true)
  }

  const onlyNumberKey = (event: any) => {
    if (props.name === 'creditCard'
      || props.name === 'expiryDate'
      || props.name === 'cvc'
      || props.name === 'bsb'
      || props.name === 'accountNumber'
      || props.name === 'postcode') {
      inputNumberOnly(event)
    }
  }

  const showDropdownList = () => {
    if (props.name === 'gender' || props.name === 'employmentSatatus' || props.name === 'dayPaymentDeducted' || props.name === 'frequencyPaymentDeducted') {
      setIsShow(true)
    }
  }

  const clickOut = useOnclickOutside(() => {
    setIsShow(false)
  })

  const { message = {}, label, tooltipName, labelNotRequire } = props
  const msg = getMessageByName(message[props.name], inputSuccess)

  let widthSize = ''
  let tooltipTitle = ''
  let tooltip_message = ''
  if (tooltipName === 'cvc') {
    widthSize = tooltipData.cvc[0].widthSize
    tooltipTitle = tooltipData.cvc[0].tooltipTitle
    tooltip_message = tooltipData.cvc[0].tooltip_message
  }

//   const data = useStaticQuery(graphql`
//   query handupSVGQuery {
//     allImageSharp {
//       edges {
//         node {
//           fixed(width: 20) {
//             src
//           }
//         }
//       }
//     }
//   }
// `)
//   const hoverIcon1 = data.allImageSharp.edges.find((each: any) => {
//     return each.node.fixed.src.includes("hand-point-up-1.png")
//   })

  return (
    <>
      {labelNotRequire && <Label>{labelNotRequire}</Label>}
      {label &&
        <Label>{label}
          <LabelIcon onInputSucessAsLabelIconColor={inputSuccess}>{'  '}*</LabelIcon>
        </Label>}
      <HoveringWrapper
        hoverIcon={img}>
        <InputField
          onChange={event => handleValidation(event)}
          onBlur={event => onSubmit(event)}
          placeholder={props.placeholder}
          widthSize={props.widthSize}
          paddingSize={props.paddingSize}
          name={props.name}
          onInputSucess={props.onInputSucess ? props.onInputSucess(inputSuccess) : {}}
          onVisibility={props.onVisibility ? props.onVisibility(visibility) : {}}
          type={props.type}
          maxLength={props.maxLength}
          onInputSucessAsBackgroundColor={inputSuccess}
          onTypeText={props.onTypeText ? props.onTypeText(textOutput) : {}}
          value={textOutput}
          onClick={
            showDropdownList
          }
          onShow={props.onShow ? props.onShow(isShow) : {}}
          onKeyPress={event => onlyNumberKey(event)}
          readOnly={props.readOnly}
          ref={aRef}
        // clickOut={clickOut}
        />
      </HoveringWrapper>
      {tooltipName && <Tooltip {...{ tooltipName, widthSize, tooltipTitle, tooltip_message, visibility }} />}
      {msg
        && <ValidationIndicator
          isCorrect={inputSuccess} {...{ message: msg, visibility }}
        />}
    </>
  )
})

export default TextInput